body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
#container{width: 500px;border: 15px solid rgb(230, 230, 243);border-radius: 15px;position: relative;left: 370px;}
h1{color: green;text-align: center;}
label{text-align: left;margin-left: 15px;margin-bottom: 10px;}
input,select,textarea{margin-left: 15px;width: 440px;}
p{font-size: small;padding-left: 15px;;}
#fil{border: 1px solid gray;}
textarea{height: 100px;}
button{display: inline-block;text-align: center;background-color: green;padding: 15px 25px;}
#ab{margin-left: 140px;}
#ba{margin-left: 50px;}
#gender{ width:50px;}
